@tailwind base;
@tailwind components;
@tailwind utilities;

/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Body Defaults */
  body {
    font-family: 'Inter', sans-serif; /* Or replace with your preferred font */
    font-size: 16px;
    line-height: 1.6;
    background-color: #000; /* Matches the black background of your app */
    color: #fff; /* Default text color for readability */
    overflow-x: hidden; /* Prevent horizontal scrollbars */
  }
  
  /* Responsive Images */
  img {
    max-width: 100%;
    height: auto;
    display: block;
  }
  
  /* Responsive Flexbox Container Defaults */
  .flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  
  /* Spacing for Small Screens */
  .container {
    padding: 1rem;
    margin: 0 auto;
    max-width: 100%;
  }
  
  /* Universal Button Styles */
  button {
    background-color: #4f46e5; /* Default Tailwind Indigo-600 */
    color: #fff;
    border: none;
    border-radius: 0.375rem; /* Rounded corners */
    padding: 0.5rem 1rem;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }
  
  button:hover {
    background-color: #4338ca; /* Darker Indigo for hover effect */
    transform: scale(1.05); /* Slight zoom effect */
  }
  
  button:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.5); /* Focus ring */
  }
  
  /* Typography Responsiveness */
  h1, h2, h3, h4, h5, h6 {
    font-weight: 700;
    line-height: 1.3;
  }
  
  h1 {
    font-size: 2.5rem;
  }
  
  h2 {
    font-size: 2rem;
  }
  
  h3 {
    font-size: 1.75rem;
  }
  
  @media (max-width: 768px) {
    h1 {
      font-size: 2rem;
    }
  
    h2 {
      font-size: 1.5rem;
    }
  
    h3 {
      font-size: 1.25rem;
    }
  }
  
  /* Links */
  a {
    color: #4f46e5;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  a:hover {
    color: #4338ca;
  }
  
  /* Utility for Sections */
  .section {
    padding: 4rem 1rem;
  }
  
  .section.dark {
    background-color: #1a1a1a; /* Dark gray for sections */
  }
  
  .section.light {
    background-color: #f9fafb; /* Light gray for sections */
    color: #000;
  }
  